@import '../../assets/css/variable.less';
@border: 1px solid @divider_color;

* {
  box-sizing: border-box;
}

.pool-detail {
  .back-pool {
    height: 108px;
    border-top: @border;
    display: flex;
    font-size: 24px;
    
    a {
      text-decoration: none;
    }

    .icon-back {
      height: 48px;
      display: flex;
      align-items: center;

      span {
        color: #FFFFFF;
      }
    }

    img {
      width: 24px;
      height: 24px;
      margin-right: 16px;
    }
  }

  .pool-deatail-info {
    .WBNB,
            .WMATIC,
            .amUSDC,
            .BUSD,
            .USDT,
            .CAKE,
            .DERI,
            .TBUSD,
            .TDERI {
              background-size: 20px 20px;
            }

            .DERI,
            .TDERI {
              background-image: url('../../assets/img/logo.svg');
              background-repeat: no-repeat;
              background-position: center;
            }

            .WBNB {
              background-image: url('./img/WBNB.svg');
              background-repeat: no-repeat;
              background-position: center;
            }

            .WMATIC {
              background-image: url('./img/matic.svg');
              background-repeat: no-repeat;
              background-position: center;
            }

            .amUSDC {
              background-image: url('./img/amusdc.svg');
              background-repeat: no-repeat;
              background-position: center;
            }

            .BUSD,
            .TBUSD {
              background-image: url('./img/BUSD.svg');
              background-repeat: no-repeat;
              background-position: center;
            }

            .USDT {
              background-image: url('./img/USDT.svg');
              background-repeat: no-repeat;
              background-position: center;
            }

            .CAKE {
              background-image: url('./img/Pancake.svg');
              background-repeat: no-repeat;
              background-position: center;
            }
    .pool-detail-basetoken-info {
      .select-basetoken-info {
        .select-coin-title {
          padding-left: 24px;
          color: @whiteFontColor;
          font-size: 18px;
        }

        .coin-list {
          margin-top: 16px;

          .coin-box {
            height: 100px;
            cursor: pointer;
            border-radius: 4px;
            padding: 0 24px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            border: 1px solid transparent;

            .base-apy-info {
              display: flex;
              align-items: center;

              .apy-base-staked {
                .base-name {
                  color: @whiteFontColor;
                  font-size: 18px;
                }
              }
            }

            .btoken-logo {
              width: 40px;
              height: 40px;
              background-color: rgba(255, 255, 255, 0.1);
              border-radius: 50%;
              margin-right: 16px;
              display: flex;
              align-items: center;
            }
            .lp-btoken-logo{
              display: flex;
              align-items: center;
              margin-right: 16px;
              .btoken-logo{
                margin-right: 0;
              }
              .conect-logo{
                font-size: 20px;
                margin: 0 5px;
              }
            }

            
          }

          .coin-box:hover {
            border: 1px solid @button_outline_color;
          }

        }
      }

      .pool-info-text {
        padding-left: 24px;

        .pool-network-type {
          display: flex;
          align-items: center;
          padding-top: 42px;

          .pool-type-network-text {
            margin-left: 12px;
            font-weight: 600;

            .pool-type-text {
              color: #FFFFFF;
            }

            .pool-network-text {
              font-size: 14px;
            }
          }

          .network-logo {
            img {
              width: 28px;
              height: 28px;
            }
          }
        }

        .pool-des {
          padding-top: 47px;
          padding-right: 74px;
          font-size: 14px;

          .learn-more {
            color: @button_outline_color;
          }
        }
      }
    }

    .pool-detail-add-remove-info {
      background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

      .mining-deri-liquidity {
        .btoken-liquidity {
          display: flex;
          align-items: center;
          padding-left: 24px;
          padding-top: 40px;

          .manage {
            font-size: 24px;
            color: @whiteFontColor;
            margin-left: 16px;
            height: 38px;
            line-height: 38px;
          }

          .stake {
            width: 80px;
            height: 20px;
            margin-left: 16px;
            display: flex;
            align-items: center;
            font-size: 12px;
            justify-content: space-between;
            padding: 12px;
            border: 1px solid rgba(224, 236, 255, 0.5);
            border-radius: 100px;
          }

          .btoken-logo {
            width: 40px;
            height: 40px;
            background-color: #203B60;
            border-radius: 50%;
          }
          .lp-btoken-logo{
            display: flex;
            align-items: center;
            .btoken-logo{
              margin-right: 0;
            }
            .conect-logo{
              font-size: 20px;
              margin: 0 5px;
            }
          }

         
        }

        .deri-current-epoch {
          .mining-claim {
            border-bottom: 0;
            height: 100px;

            .mining-claim-info {
              width: 100%;
              height: 100px;
              margin-top: 33px;
              align-items: initial;
              padding-left: 24px;

              .deri-num-slider {
                .deri-claim-time {
                  font-size: 22px;
                }
              }
            }
          }
        }
      }

      .mining-add-remove-liquidity {
        .pool-info {
          .pool-info-title {
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: @whiteFontColor;
            font-size: 18px;

            .down-up {
              display: block;
              cursor: pointer;

              .up-icon {
                transform: rotate(180deg);
              }
            }
          }

          .show {
            display: block;
          }

          .hide {
            display: none;
          }

          .pool-info-text-one {
            padding-top: 16px;
            padding-bottom: 24px;
          }

          .pool-info-text-one,
          .pool-info-text-two {
            .info-box {
              display: flex;
              height: 40px;
              justify-content: space-between;
              font-size: 14px;
              align-items: center;

              .info-box-num {
                color: @whiteFontColor;
              }
              .symbols{
                max-width: 204px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
            }
          }
        }
      }
    }
  }
}


@media only screen and (max-width:759px) {
  .pool-detail {
    min-width: 375px;

    .back-pool {
      display: flex;
      align-items: center;
      padding-top: 16px;
      padding-left: 24px;
      height: 48px;
    }

    .pool-deatail-info {
      .pool-detail-basetoken-info {
        .pool-info-text {
          border-bottom: @border;

          .pool-network-type {
            padding-top: 24px;
          }

          .pool-des {
            padding-top: 16px;
            margin-bottom: 16px;
          }
        }

        .select-basetoken-info {
          padding: 0 24px;

          .select-coin-title {
            margin-top: 24px;
            width: 221px;
            height: 49px;
            padding-left: 0;
          }

          .coin-list {
            min-width: 327px;
            display: flex;
            border: @border;
            border-radius: 4px;
            margin: 24px auto;

            .coin-box {
              min-width: 109px;
              height: 92px;
              flex: 1;
            }

            .coin-box:hover {
              border-radius: 4px;
            }
          }
        }
      }

      .pool-detail-add-remove-info {
        border-top: @border;

        .mining-deri-liquidity {
          border-bottom: @border;

          .btoken-liquidity {
            padding-top: 18px;
          }

          .deri-current-epoch {
            margin-top: -24px;
            height: 86px;

            .mining-claim {
              height: 86px;
              padding: 0;
            }
          }
        }

        .mining-add-remove-liquidity {
          .pool-info {
            min-height: 78px;
            border-bottom: @border;
            padding: 0 24px;
            padding-top: 24px;

            .pool-info-title {
              height: 30px;
              line-height: 30px;
            }
          }

          .width {
            width: 100%;
          }
        }
      }
    }
  }
}

@media only screen and (min-width : 760px) and (max-width:1140px) {
  .pool-detail {
    .back-pool {
      padding-left: 48px;
      padding-top: 24px;
    }

    .pool-deatail-info {
      width: 760px;
      border: @border;
      margin: 0 auto;
      display: flex;

      .pool-detail-basetoken-info {
        width: 380px;

        .pool-info-text {
          height: 234px;
          border-bottom: @border;
        }

        .select-basetoken-info {
          .select-coin-title {
            width: 221px;
            padding-top: 32px;
            color: @whiteFontColor;
          }
        }
      }

      .pool-detail-add-remove-info {
        width: 380px;
        border-left: @border;

        .mining-deri-liquidity {
          height: 234px;
          border-bottom: @border;
        }

        .mining-add-remove-liquidity {
          .pool-info {
            width: 380px;
            padding: 0 24px;
            border-right: @border;
            border-bottom: @border;
            min-height: 97px;

            .pool-info-title {
              height: 48px;
              padding-top: 32px;
            }
          }
        }
      }
    }
  }
}

@media only screen and (min-width : 1140px) {
  .pool-detail {
    .back-pool {
      padding-left: 48px;
      padding-top: 24px;
    }

    .pool-deatail-info {
      width: 1140px;
      height: 691px;
      border: @border;
      margin: 0 auto;
      display: flex;

      .pool-detail-basetoken-info {
        width: 380px;

        .pool-info-text {
          height: 234px;
          border-bottom: @border;
        }

        .select-basetoken-info {
          height: 457px;

          .select-coin-title {
            width: 221px;
            padding-top: 32px;
            color: @whiteFontColor;
          }
        }
      }

      .pool-detail-add-remove-info {
        width: 760px;
        height: 691px;
        border-left: @border;

        .mining-deri-liquidity {
          height: 234px;
          border-bottom: @border;

          .manage-stake {
            display: flex;
            align-items: center;

          }
        }

        .mining-add-remove-liquidity {
          display: flex;
          height: 457px;

          .pool-info {
            width: 380px;
            padding: 0 24px;
            border-right: @border;

            .pool-info-title {
              height: 48px;
              margin-top: 17px;

              .down-up {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}